*{margin:0px;padding:0px;}
body{
  line-height:30px;
  color:#333;
  font-size:13px;
}
ul,ol,li{list-style:none;}

.header{
  height:60px;background:rgb(23, 104, 255);
  position:fixed;
  width:100%;
  top:0px;left:0px;
  box-shadow:0px 0px 10px #969696;
  z-index:1001;
}
.header > h1{
  text-align:center;
  font-size:20px;
  color:#fff;
  line-height:60px;
}
.toggle{
  position: absolute;
  top:6px;  right: 20px;
  z-index: 2000;  width: 48px;
  height: 48px;
  color: #fff;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  cursor: pointer;
  background:none;
}

main{margin:34px 0px 0px 10px;}
.title2{
  font-size:18px;color:rgb(23, 104, 255);
  border-top:2px solid rgb(23, 104, 255);
  border-bottom:2px solid rgb(23, 104, 255);
  margin:20px 0px 10px 0px;padding:5px 0px;
}
.title3{font-size:17px;padding-top:20px;}
main ul{margin:6px 26px 16px 24px;}
main ul > li{list-style-type: disc;}
h4{margin:10px 0px;padding:2px;}

.footer{
  background:#333;
  color:#fff;
  text-align:center;
  line-height:36px;
}
.footer address{font-style: normal;}